<template><section><h1>鹰眼</h1>
<p><code v-pre>vc-map-overview</code> 组件用于加载鹰眼部件。<strong>注意：</strong> 使用此组件需要引入 <code v-pre>import 'vue-cesium/lib/vc-map-overview.css'</code>。此组件默认没有引入，需要单独引入。</p>
